@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container{max-width:960px}.site-header{background-color:rgba(0,0,0,0.85);-webkit-backdrop-filter:saturate(180%) blur(20px);backdrop-filter:saturate(180%) blur(20px)}.site-header a{color:#999;transition:ease-in-out color .15s}.site-header a:hover{color:#fff;text-decoration:none}.product-device{position:absolute;right:10%;bottom:-30%;width:300px;height:540px;background-color:#333;border-radius:21px;transform:rotate(30deg)}.product-device::before{position:absolute;top:10%;right:10px;bottom:10%;left:10px;content:"";background-color:rgba(255,255,255,0.1);border-radius:5px}.product-device-2{top:-25%;right:auto;bottom:0;left:5%;background-color:#e5e5e5}.flex-equal>*{flex:1 1}@media (min-width: 768px){.flex-md-equal>*{flex:1 1}}.overflow-hidden{overflow:hidden}

.footer-distributed{background-color:#292c2f;box-shadow:0 1px 1px 0 rgba(0,0,0,0.12);box-sizing:border-box;width:100%;text-align:left;font:bold 16px sans-serif;padding:55px 50px;margin-top:80px}.footer-distributed .footer-left,.footer-distributed .footer-center,.footer-distributed .footer-right{display:inline-block;vertical-align:top}.footer-distributed .footer-left{width:40%}.footer-distributed h3{color:#ffffff;font:normal 36px 'Cookie', cursive;margin:0}.footer-distributed h3 span{color:#5383d3}.footer-distributed .footer-links{color:#ffffff;margin:20px 0 12px;padding:0}.footer-distributed .footer-links a{display:inline-block;line-height:1.8;text-decoration:none;color:inherit}.footer-distributed .footer-company-name{color:#8f9296;font-size:14px;font-weight:normal;margin:0}.footer-distributed .footer-center{width:35%}.footer-distributed .footer-center i{background-color:#33383b;color:#ffffff;font-size:25px;width:38px;height:38px;border-radius:50%;text-align:center;line-height:42px;margin:10px 15px;vertical-align:middle}.footer-distributed .footer-center i.fa-envelope{font-size:17px;line-height:38px}.footer-distributed .footer-center p{display:inline-block;color:#ffffff;vertical-align:middle;margin:0}.footer-distributed .footer-center p span{display:block;font-weight:normal;font-size:14px;line-height:2}.footer-distributed .footer-center p a{color:#5383d3;text-decoration:none}.footer-distributed .footer-right{width:20%}.footer-distributed .footer-company-about{line-height:20px;color:#92999f;font-size:13px;font-weight:normal;margin:0}.footer-distributed .footer-company-about span{display:block;color:#ffffff;font-size:14px;font-weight:bold;margin-bottom:20px}.footer-distributed .footer-icons{margin-top:25px}.footer-distributed .footer-icons a{display:inline-block;width:35px;height:35px;cursor:pointer;background-color:#33383b;border-radius:2px;font-size:20px;color:#ffffff;text-align:center;line-height:35px;margin-right:3px;margin-bottom:5px}@media (max-width: 880px){.footer-distributed{font:bold 14px sans-serif}.footer-distributed .footer-left,.footer-distributed .footer-center,.footer-distributed .footer-right{display:block;width:100%;margin-bottom:40px;text-align:center}.footer-distributed .footer-center i{margin-left:0}}

*{padding:0;margin:0}html{background-color:#eaf0f2}body{font:14px/1.5 Arial, Helvetica, sans-serif}header{text-align:center;padding-top:100px;margin-bottom:200px}header h1{font:normal 32px/1.5 'Open Sans', sans-serif;color:#3F71AE;padding-bottom:16px}header h2{color:#F05283}header h2 a{color:inherit;text-decoration:none;display:inline-block;border:1px solid #F05283;padding:10px 15px;border-radius:3px;font:bold 14px/1 'Open Sans', sans-serif;text-transform:uppercase}header h2 a:hover{background-color:#F05283;transition:0.2s;color:#fff}header ul{max-width:600px;margin:60px auto 0}header ul a{text-decoration:none;color:#FFF;text-align:left;background-color:#B9C1CA;padding:10px 16px;border-radius:2px;opacity:0.8;font-size:16px;display:inline-block;margin:4px;line-height:1;outline:none;transition:0.2s ease}header ul li a.active{background-color:#66B650;pointer-events:none}header ul li a:hover{opacity:1}header ul{list-style:none;padding:0}header ul li{display:inline-block}@media (max-height: 800px){footer{position:static}header{padding-top:40px}}

body{margin:0px;background:#010101}.loaded{-webkit-user-select:none;margin:auto;cursor:zoom-in}

